<template>
  <main>
    <div id="content">
      <div>
        <h2>Iniciar sesin</h2>

        <form method="POST" class="contact-form" @submit.prevent="validationForm">
          <label for="user_input">
            usuario, correo o RUT
            <span v-if="!$v.inputUser.required">*</span>
          </label>
          <input
            id="user_input"
            type="text"
            name="user_input"
            v-model.lazy="$v.inputUser.$model "
            :class="{'invalidInput': $v.inputUser.$error}"
          />

          <label for="user_password">
            Contasea
            <span v-if="!$v.passwordUser.required">*</span>
          </label>
          <input
            id="user_password"
            type="password"
            name="user_password"
            v-model.lazy="$v.passwordUser.$model"
            :class="{'invalidInput': $v.passwordUser.$error}"
          />

          <button type="submit">Ingresar</button>

          <p id="error" v-if="message !=''">{{ message }}</p>
        </form>

        <a href="#">Recuperar contrasea</a>
      </div>
    </div>
  </main>
</template>

<script>
import { required, minLength } from "vuelidate/lib/validators";

export default {
  name: "Login",
  components: {},
  data() {
    return {
      inputUser: "",
      passwordUser: "",
      sending: false,
      body: {},
      message: ""
    };
  },
  validations: {
    inputUser: { required, minLength: minLength(4) },
    passwordUser: { required, minLength: minLength(6) }
  },
  mounted() {},
  methods: {
    validationForm() {
      this.$v.$touch();

      if (this.$v.$invalid) {
        this.message = "Campos no valido";
      } else {
        this.sending = true;

        this.body = {
          input: this.inputUser,
          password: this.passwordUser
        };
        this.login();
      }
    },
    login() {
      this.axios
        .post("login/", this.body)
        .then(res => {
          console.log(res);
          this.sending = false;
          this.inputUser = "";
          this.passwordUser = "";
        })
        .catch(error => {
          this.message = error.response.data.message;

          this.sending = false;
        });
    }
  },
  watch: {}
};
</script>

